export default class Light {
    #lights = new Map();


    #scene = null;

    constructor(scene){
        this.#scene = scene;
    }

    add(name, light){
        this.#lights.set(name, light);
        this.#scene.add(light);
    }

    remove(name){
        let light = this.#lights.get(name);
        this.#scene.remove(light);
        this.#lights.delete(name);
    }

    get(name){
        return this.#lights.get(name);
    }
}
