

import Canvas2Image from '../utils/Canvas2Image';
import ModelLoader from '../model/ModelLoader';
import Light from "../light/Light";
import Viewpoint from "../viewpoint/Viewpoint";



let THREE = window.THREE = require('three');
require('three/examples/js/controls/OrbitControls');

require('three/examples/js/loaders/MTLLoader');
require('three/examples/js/loaders/OBJLoader');
require('three/examples/js/loaders/DRACOLoader');
require('three/examples/js/loaders/GLTFLoader');
require('three/examples/js/loaders/PLYLoader');

require('three/examples/js/shaders/CopyShader');
require('three/examples/js/postprocessing/EffectComposer');
require('three/examples/js/postprocessing/RenderPass');
require('three/examples/js/postprocessing/ShaderPass');
require('three/examples/js/shaders/FXAAShader');

const d3 = Object.assign({},
    require('d3-scale'), require('d3-timer'), require('d3-array'));





let duration_changePerspective = 1000;


let resolveEnvelopeBoxFnMap = new WeakMap();

let envelopeBoxPromiseMap = new WeakMap();



let matColorMap = new Map();


const defaultProps = {
    shadowMapEnabled: false,
    antialias: true,
    alpha: true,
    preserveDrawingBuffer: true,
    autoClear: true
};


let render = Symbol();




export default class Scene extends THREE.Scene{


    models = new ModelLoader(this);

    boundingBox = new THREE.Box3();

    
    light = null;

    camera = null;

    #viewpoint = new Viewpoint(this);





    constructor(container,options){
        super();

   
        this.boundingBox = this.models.boundingBox;

        this.light =new Light(this, 0.1);



        let _option = Object.assign({}, defaultProps, options);

   
        let {width, height} = container.getBoundingClientRect();
        this.width = width;
        this.height = height;
   
        this.camera = new THREE.PerspectiveCamera( 45, width/height, 0.1, 10000000 );




        const {antialias, alpha, preserveDrawingBuffer, autoClear} = _option;

        this.renderer = new THREE.WebGLRenderer({antialias, alpha, preserveDrawingBuffer, autoClear});
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(width, height);
        this.renderer.shadowMap.enabled = _option.shadowMapEnabled;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

        container.appendChild(this.renderer.domElement);
      
        var renderPass = new THREE.RenderPass( this.scene, this.camera );
        this.fxaaPass = new THREE.ShaderPass( THREE.FXAAShader );
        var pixelRatio = this.renderer.getPixelRatio();
        this.fxaaPass.material.uniforms[ 'resolution' ].value.x = 1 / ( width * pixelRatio );
        this.fxaaPass.material.uniforms[ 'resolution' ].value.y = 1 / ( height * pixelRatio );
        this.effectComposer = new THREE.EffectComposer( this.renderer );
        this.effectComposer.addPass( renderPass );
        this.effectComposer.addPass( this.fxaaPass );


        this[render]();

        let orbitControls = new THREE.OrbitControls(this.camera, this.renderer.domElement);
        orbitControls.autoRotate = false;


        envelopeBoxPromiseMap.set(this, new Promise(resolve => {
            resolveEnvelopeBoxFnMap.set(this, function () {
                resolve(this);
            })
        }));



        this.light.add('ambient', new THREE.AmbientLight(0xFFFFFF, 1));

    }


    [render](){
        let fn = ()=>{
            requestAnimationFrame(this[render]);



            this.renderer.render(this, this.camera);
            

        };
        this[render] = fn;
        fn();
    }

    /**
     * 
     * @param orientation BOTTOM|TOP|LEFT|RIGHT|FRONT|AFTER|INIT
     */
    lookFrom(orientation){
        this.#viewpoint.changeViewpoint(orientation.toUpperCase());
    }

    flyTo(camera, options){
        this.#viewpoint.flyTo(camera, options);
    }

    /**
     *
     * @param x
     * @param y
     */
    pick(x,y){
        return this.models.pick(x,y);
    }

    pickPOI(x,y){
        return this.models.pickPOI(x,y);
    }


    /**
     * 
     */
    saveAsPng(){
        let canvas = this.scene.renderer.domElement;
        Canvas2Image.saveAsPNG(canvas, canvas.width, canvas.height);
    }

    /**
     *
     * @returns {*|*|void}
     */
    getImg(){
        let canvas = this.scene.renderer.domElement;
        return Canvas2Image.convertToPNG(canvas, canvas.width, canvas.height);

    }

    generateSprite(e){
        console.log('e',e);
        let canvas = document.createElement('canvas');
        canvas.width = 16;
        canvas.height = 16;

        let context = canvas.getContext('2d');
        let gradient = context.createRadialGradient(canvas.width / 2, canvas.height / 2, 0, canvas.width / 2, canvas.height / 2, canvas.width / 2);
        gradient.addColorStop(0, 'rgba(255,255,255,1)');
        gradient.addColorStop(0.2, 'rgba(0,255,255,1)');
        gradient.addColorStop(0.4, 'rgba(0,0,64,1)');
        gradient.addColorStop(1, 'rgba(0,0,0,1)');

        context.fillStyle = gradient;
        context.fillRect(0, 0, canvas.width, canvas.height);

        let texture = new THREE.Texture(canvas);
        texture.needsUpdate = true;
        return texture;
    }

    on(type, fun){
        const mouseEvents = ['click', 'mouseover', 'dblclick'];

        if (mouseEvents.includes(type)){
            this.renderer.domElement.addEventListener(type, fun);
        }
    }

    /**
     * 
     * @param viewpoint
     */
    changeViewpoint(viewpoint){
        this.camera.fov = viewpoint.fov;
        let position = viewpoint.position;
        this.camera.position.set(position.x, position.y, position.z);
        let rotation = viewpoint.rotation;
        this.camera.rotation.set(rotation.x, rotation.y, rotation.z);
    }

    /**
     * 
     * @param opacity
     */
    setOpacity(opacity){
        envelopeBoxPromiseMap.get(this).then(_=>{
            Object.values(this.materials.materials).forEach( m=>{
                m.opacity = opacity;
                m.transparent = true;
            } );
        });
    }

    /**
     * 
     * @param visible
     */
    setVisible(visible){
        envelopeBoxPromiseMap.get(this).then(_=>{
            this.model.visible = visible;
        });
    }

    /**
     * 
     * @param clientX
     * @param clientY
     * @returns {*}
     */
    getMeshByClientXY(clientX, clientY){
        return fetchObjects.call(this, clientX, clientY);
    }

    /**
     * 
     */
    changeBackground(value){
        if (typeof value === 'string') {
            if (value.indexOf('.') === -1) {
                this.background = new THREE.Color(value);
            }else {
                let texture = new THREE.TextureLoader().load( value );
                texture.wrapS = THREE.RepeatWrapping;
                texture.wrapT = THREE.RepeatWrapping;
      
                this.background = texture;
            }

        }
    }



}
