let THREE = window.THREE = require('three');
require('three/examples/js/controls/OrbitControls');

require('three/examples/js/loaders/MTLLoader');
require('three/examples/js/loaders/OBJLoader');
require('three/examples/js/loaders/DRACOLoader');
require('three/examples/js/loaders/GLTFLoader');
require('three/examples/js/loaders/PLYLoader');
require('three/examples/js/libs/inflate.min');
require('three/examples/js/loaders/FBXLoader');




THREE.DRACOLoader.setDecoderPath( 'libs/draco/' );
THREE.DRACOLoader.setDecoderConfig( { type: 'js' } );

const d3 = Object.assign({},
    require('d3-scale'), require('d3-timer'), require('d3-array'));


export default class ModelLoader {


    #group = new THREE.Group();


    #geometries = new THREE.Group();
    #scene = null;
    #timer = null;

    boundingBox = new THREE.Box3();

    constructor(scene){
        this.#scene = scene;
        this.#group.add(this.#geometries);
        this.#scene.add(this.#group);
    }

    addMesh(mesh){
        if (!(mesh instanceof THREE.Mesh)) {
            throw new Error('mesh is not a THREE.Mesh instance');
        }
        this.#geometries.add(mesh);
        this.boundingBox.expandByObject(this.#group);
    }


    addOBJModel({objUrl,materialUrl,onProgress, onError}){
        if (!materialUrl) {
            return new Promise(resolve => {
                new THREE.OBJLoader()
                    .load(objUrl, loadedMesh => {
                        loadedMesh.children.forEach(function (child) {
                            child.material = new THREE.MeshLambertMaterial({color: '#808080', side:THREE.DoubleSide});
                            child.geometry.computeFaceNormals();
                            child.geometry.computeVertexNormals();
                        });
                        console.log('loadedMesh',loadedMesh);


                        this.#addModel(loadedMesh);
                        resolve(loadedMesh);
                     
                    },onProgress,onError);
            });
        }else {
            return new Promise(resolve => {
                new THREE.MTLLoader()
                    .load(materialUrl, mat => {
                        mat.preload();
                        new THREE.OBJLoader()
                            .setMaterials(mat)
                            .load(objUrl, loadedMesh => {
                           


                                this.#addModel(loadedMesh);
                                resolve(loadedMesh);
                              
                            },onProgress,onError);
                    });
            });
        }

    }


    addFBXModel({url,onProgress, onError}){
        return new Promise(resolve => {
            new THREE.FBXLoader()
                .load(url, loadedMesh => {
                    this.#addModel(loadedMesh);
                    resolve(loadedMesh);
                    // resolveEnvelopeBoxFnMap.get(this)();
                },onProgress,onError);
        });
    }


    addGLTFModel(glbUrl){
        let loader = new THREE.GLTFLoader();
        loader.setDRACOLoader( new THREE.DRACOLoader() );

        return new Promise(resolve => {
            // Load a glTF resource
            loader.load(glbUrl, ( data ) => {

                    const gltf = data;

                    var object = gltf.scene;

                   //  #addModel.call(this, object);
                    resolve(object);
                    // resolveEnvelopeBoxFnMap.get(this)();
                },
                // called while loading is progressing
                function ( xhr ) {

                    console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

                },
                // called when loading has errors
                function ( error ) {

                    console.log( 'An error happened', error );

                }
            );
        });
    }


    addDRACOModel(drcUrl){
        let dracoLoader = new THREE.DRACOLoader();

        return new Promise(resolve => {
            dracoLoader.load( drcUrl, ( geometry ) => {

                geometry.computeVertexNormals();

                let material = new THREE.MeshStandardMaterial( { color: 0x606060 } );
                let mesh = new THREE.Mesh( geometry, material );
                mesh.castShadow = true;
                mesh.receiveShadow = true;

                // #addModel.call(this, mesh);
                resolve(mesh);
                // resolveEnvelopeBoxFnMap.get(this)();

                // Release decoder resources.
                THREE.DRACOLoader.releaseDecoderModule();

            } );
        });

    }


    addPLYModel(url){
        let loader = new THREE.PLYLoader();

        return new Promise(resolve => {
            // Load a glTF resource
            loader.load(url, ( geometry ) => {

                    geometry.computeVertexNormals();
                    var material = new THREE.MeshStandardMaterial( { color: 0x0055ff, flatShading: true } );
                    var mesh = new THREE.Mesh( geometry, material );

                    // #addModel.call(this, mesh);
                    resolve(mesh);
                    // resolveEnvelopeBoxFnMap.get(this)();
                }
            );
        });
    }

    addPLY(ply){
        let loader = new THREE.PLYLoader();

        let scale = d3.scaleLinear().domain([1,10]).range([0.000001, 0.1]);

        this.scene.callbacks.push(_=>{
            let cameraPosition = this.camera.position;
            let distance = Math.sqrt(
                Math.pow(cameraPosition.x,2)+
                Math.pow(cameraPosition.y,2)+
                Math.pow(cameraPosition.z,2)
            );


            let level = ply.computeLevel(distance);


            let plyObj = ply.getZoomedText(level);

            if (plyObj.needUpdate) {
                let text = plyObj.text;

                let geometry = loader.parse(text);
                geometry.computeVertexNormals();

                let material = new THREE.PointCloudMaterial({
                    size: scale(plyObj.zoom),
                    transparent: true,
                    opacity: 0.2,
                    vertexColors: true,
                    sizeAttenuation: true,
                    color: 0xffffff
                });


                this.model.remove(this.cloud);

                this.cloud = new THREE.PointCloud(geometry, material);

                this.model.add(this.cloud);

            }

        });

        let text = ply.getZoomedText(5).text;

        return new Promise(resolve => {

            let geometry = loader.parse(text);

            geometry.computeVertexNormals();


            let material = new THREE.PointCloudMaterial({
                size: 0.01,
                transparent: true,
                opacity: 0.2,
                vertexColors: true,
                sizeAttenuation: true,
                color: 0xffffff
            });

            this.cloud = new THREE.PointCloud(geometry, material);

            // #addModel.call(this, this.cloud);

            document.getElementById('loading').style.display = 'none';
            resolve(this.cloud);
            // resolveEnvelopeBoxFnMap.get(this)();

        });
    }


    stretchVertical(duration = 1000){
        console.log(this.#group.children);

        let height = this.boundingBox.getBoundingSphere().radius * Math.sin(Math.PI/4);

        let scale = d3.scaleLinear().domain([0, this.#group.children.length-1]).range([0,height]);

        let heightScales = this.#group.children.map((model, i)=>{
            return d3.scaleLinear().domain([0, duration]).range([0, scale(i)]);
        });

        if (this.#timer) {
            this.#timer.stop();
        }
        this.#timer = d3.timer(elapsed=>{
            this.#group.children.forEach((model, i)=>{
                model.position.setY(heightScales[i](elapsed));
            });

            if (elapsed > duration) this.#timer.stop();
        });

    }


    resumeStretch(duration = 1000){
        if (this.#timer) {
            this.#timer.stop();
        }


        let heightScales = this.#group.children.map(model=>{
            return d3.scaleLinear().domain([0, duration]).range([model.position.y, 0]);
        });

        this.#timer = d3.timer(elapsed=>{
            this.#group.children.forEach((model, i)=>{
                model.position.setY(heightScales[i](elapsed));
            });

            if (elapsed > duration) this.#timer.stop();
        });
    }

    showModel(model){
        if (model) {
            let target = this.#group.children.find(m=>m===model);
            if (target) {
                this.#group.children.forEach(m=>{
                    m.visible = false;
                });
                target.visible = true;
            }

        }else {
            this.#group.children.forEach(m=>{
                m.visible = true;
            });
        }
    }

    /**
     *
     * @param x
     * @param y
     */
    pick(x,y){
        let raycaster = new THREE.Raycaster();
        let mouse = new THREE.Vector2();


        mouse.x = (x/window.innerWidth)*2 -1;
        mouse.y = -(y/window.innerHeight)*2 + 1;

        raycaster.setFromCamera(mouse,this.#scene.camera);

        let intersectObjs = [];
        this.#group.children.forEach(c=>{
            intersectObjs.push(...raycaster.intersectObjects(c.children))
        });
        return intersectObjs;
    }
    pickPOI(x,y){
        let raycaster = new THREE.Raycaster();
        let mouse = new THREE.Vector2();


        mouse.x = (x/window.innerWidth)*2 -1;
        mouse.y = -(y/window.innerHeight)*2 + 1;

        raycaster.setFromCamera(mouse,this.#scene.camera);


        return raycaster.intersectObjects(this.#geometries.children);
    }


    getObjectById(id){
        return this.#group.getObjectById(id) || this.#geometries.getObjectById(id);
    }


    getObjectByName(name){
        let meshs = [];
        meshs.push(this.#group.getObjectByName(name));
        meshs.push(this.#geometries.getObjectByName(name));

        meshs = meshs.filter(mesh=>!!mesh);
        return meshs;
    }


    getObjectByProperty(name, value){
        let meshs = [];
        meshs.push(this.#group.getObjectByProperty(name, value));
        meshs.push(this.#geometries.getObjectByProperty(name, value));

        meshs = meshs.filter(mesh=>!!mesh);
        return meshs;
    }

    #addModel = (model) => {
        this.#group.add(model);
        this.boundingBox.expandByObject(this.#group);

    }
}

