Promise.all(urls.map(url=>d3.json('./data/'+url)))
    .then(

       jsons=>{


           jsons.forEach((json, idx)=>{
               var predictionLength = Math.ceil(json.feeds.length * forecast_len_ratio);
               var forecast_time = holtWinters(json.feeds.map(feeds=>new Date(feeds.created_at).getTime()), predictionLength);
               var forecast_value = [];
               if (idx === 0 || idx === 2 || idx === 3 || idx === 4 || idx === 5){
                   forecast_value = holtWinters(json.feeds.map(feeds=>+feeds.field), predictionLength);
                   for (let i=json.feeds.length+1; i<forecast_time.augumentedDataset.length; i++){
                       json.feeds.push({created_at: forecast_time.augumentedDataset[i], field: Math.ceil(forecast_value.augumentedDataset[i])})
                   }

               }else {
                   let oldArr = new Array(100).fill(1);
                   if (idx !== 6 && idx !== 7){
                       oldArr = json.feeds.map(feeds=>+feeds.field);
                   }
                   forecast_value = holtWinters(oldArr, predictionLength);
                   for (let i=json.feeds.length+1; i<forecast_time.augumentedDataset.length; i++){
                       json.feeds.push({created_at: forecast_time.augumentedDataset[i], field: forecast_value.augumentedDataset[i]})
                   }
               }
           });


        let data = {};
        let times = [];

        let lights = [];
        let temps = [];
        let nums = [];

        jsons.forEach((json,idx)=>{
            json.feeds.forEach(feed=>{
                let time = new Date(feed.created_at);
                let has = times.find(t=>t-time === 0);
                if (!has) {
                    times.push(time);
                }
                if (idx === 0) {
                    if (has) {
                        data[time.toString()].push({type:'light', room:'Staff Room V-110', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'light', room:'Staff Room V-110', value:+feed.field}]
                    }
                    lights.push(+feed.field);
                }else if (idx === 1) {
                    if (has) {
                        data[time.toString()].push({type:'light', room:'Staff Room V-111', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'light', room:'Staff Room V-111', value:+feed.field}]
                    }
                    lights.push(+feed.field);
                }else if (idx === 2) {
                    if (has) {
                        data[time.toString()].push({type:'temp', room:'Tea Room V-116', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'temp', room:'Tea Room V-116', value:+feed.field}]
                    }
                    temps.push(+feed.field);
                }else if (idx === 3) {
                    if (has) {
                        data[time.toString()].push({type:'temp', room:'Staff Common Room V-131', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'temp', room:'Staff Common Room V-131', value:+feed.field}]
                    }
                    temps.push(+feed.field);
                }else if (idx === 4) {
                    if (has) {
                        data[time.toString()].push({type:'temp', room:'Staff Room V-154', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'temp', room:'Staff Room V-154', value:+feed.field}]
                    }
                    temps.push(+feed.field);
                }else if (idx === 5) {
                    if (has) {
                        data[time.toString()].push({type:'temp', room:'Staff Room V-159', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'temp', room:'Staff Room V-159', value:+feed.field}]
                    }
                    temps.push(+feed.field);
                }else if (idx === 6) {
                    if (has) {
                        data[time.toString()].push({type:'temp', room:'Entrance', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'temp', room:'Entrance', value:+feed.field}]
                    }
                    temps.push(+feed.field);
                }
                else if (idx === 7) {
                    if (has) {
                        data[time.toString()].push({type:'nums', room:'Tea Room V-116', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'nums', room:'Tea Room V-116', value:+feed.field}]
                    }
                    nums.push(+feed.field);
                }else {
                    if (has) {
                        data[time.toString()].push({type:'nums', room:'Staff Common Room V-131', value:+feed.field})
                    }else {
                        data[time.toString()]=[{type:'nums', room:'Staff Common Room V-131', value:+feed.field}]
                    }
                    nums.push(+feed.field);
                }
            });
        });


        times.sort((a,b)=>a-b);

        let x = d3.scaleTime().domain(d3.extent(times)).range([0,1000]);
        //let x = d3.scaleTime().domain(d3.extent(times)).nice().range([0,1000]);
        lightScale = d3.scaleLinear().domain(d3.extent(lights)).range(['#262626','#FFFFFF']);
        tempScale = d3.scaleLinear().domain(d3.extent(temps)).range(['#00B0F0','#FF0000']);

        let svg = d3.select('svg');

        let axis = d3.axisBottom(x);

        let g = svg.append('g')
            .style('transform','translate(0px, 77px)')
            .call(axis);

        let forecast_len = 1000 * forecast_len_ratio;
        let prediction_line = g.append('g');
           prediction_line.append('line')
            .attr('x1', 1000 - forecast_len)
            .attr('y1', 3)
            .attr('x2', 1000)
            .attr('y2', 3)
            .style('stroke','red')
            .style('stroke-dasharray', '5,5')

            .style('stroke-width',2);
           prediction_line.append('text')
            .attr('x', 1000 - forecast_len/2)
            .attr('y', -7)
            .html('Prediction')
            .style('fill', 'red')
               .style('font-size', 19);

        let brush = d3.brushX()
            .extent([[0, -10], [1000, 20]])
            .on('brush',brushed);

        function brushed({selection}) {
            if (selection) {
                let num = selection[0];
                let time = x.invert(num);

                let deltas = times.map(t=>({time:t, delta: Math.abs(t-time)}));
                deltas.sort((a,b)=>a.delta-b.delta);
                currentD = data[deltas[0].time.toString()];


                let lightItems = currentD.filter(d=>d.type === 'light');
                lightItems.forEach(item=>lightRoom(item));

                let tempItems = currentD.filter(d=>d.type === 'temp');
                tempItems.forEach(item=>tempRoom(item));

                let numItems = currentD.filter(d=>d.type === 'nums');
                numItems.forEach(item=>genPeople(item));


            }
       }
        const defaultSelection = [x(times[0]), x(times[1])];
        g.call(brush)
            .call(brush.move, defaultSelection);

        d3.select('.domain').style('stroke','none');
        d3.selectAll('.tick text').style('fill','#fff');
        d3.select("#loading").style('display', 'none');
    }
    );



function lightRoom(lightItem){
    let rooms = lightItem.room.split(",");
    rooms.forEach(room=>{
        let d = data.find(d=>d.name === room);
        let hidMesh = scene.models.getObjectByName(d.innerRoomName);
        let c = lightScale(lightItem.value);
        hidMeshs.push(...hidMesh);
        hidMesh.forEach(m=>{

            m.material.color = new THREE.Color( c );
            m.material.transparent = true;
            m.material.opacity = 0.5
        });
    })

}

function tempRoom(lightItem){
    let rooms = lightItem.room.split(",");
    rooms.forEach(room=>{
        let d = data.find(d=>d.name === room);
        let hidMesh = scene.models.getObjectByName(d.innerRoomName);
        let c = tempScale(lightItem.value);
        hidMeshs.push(...hidMesh);

        hidMesh.forEach(m=>{

            m.material.color = new THREE.Color( c );
            m.material.transparent = true;
            m.material.opacity = 0.5
        });
    })


}

function genPeople(item){
    let rooms = item.room.split(",");
    scene.remove(group_116);
    group_116 = new THREE.Group();
    scene.add(group_116);
    rooms.forEach(room=>{
        let d = data.find(d=>d.name === room);
        let hidMesh = scene.models.getObjectByName(d.innerRoomName);
        let box = new THREE.Box3();
        box.expandByObject(hidMesh[0]);
        let sphere = box.getBoundingSphere();
        let center = sphere.center;

        let box2 = new THREE.Box3();
        let cubeMesh = scene.models.getObjectByName(d.cube);
        box2.expandByObject(cubeMesh[0]);
        let sphere2 = box2.getBoundingSphere();

        let num = item.value;
        let r = sphere2.radius/2;

        for (let i = 0; i < num; i++) {
            let shpereGeom = new THREE.IcosahedronBufferGeometry(r,4);
            let mat = new THREE.MeshLambertMaterial( { color: "#00FF00" } );
            let mesh = new THREE.Mesh(shpereGeom, mat);
            mesh.position.set(center.x, center.y, center.z);
            group_116.add(mesh);

        }
    })

}



